import React from 'react';
import MyPostsContainer from './MyPosts/MyPostsContainer';
import ProfileInfo from './ProfileInfo/ProfileInfo';
import styles from './Profile.module.css';
import Post from './MyPosts/Post/Post';
import Profile from './Profile';
import { connect } from 'react-redux';
import {Redirect, withRouter} from 'react-router-dom';
import {getProfileThunkCreator, getStatusThunkCreator, setStatusThunkCreator} from "../../redux/profile-reducer";
import {withAuthRedirect} from "../../hoc/withLoginRedirect";
import {compose} from "redux";

class ProfileContainer extends React.Component {
  componentDidMount() {
    let userId = this.props.match.params.userId;
    if (!userId) {userId = this.props.myProfileData.id}
    // {userId=2}
    this.props.getProfileThunkCreator(userId);
    this.props.getStatusThunkCreator(userId);
}

  render() {
    return <Profile {...this.props}/>
  }
}

const mapStateToProps =(state) => {
  return {
    profilePage: state.profilePage,
    myProfileData: state.auth
  }
}

export default compose(
    connect(mapStateToProps, {getProfileThunkCreator, getStatusThunkCreator, setStatusThunkCreator}),
    withRouter,
    withAuthRedirect
)(ProfileContainer)

